
/* 定义一些公共变量 */
@main-color: #74c546;
@main-border-color: #d5ebc8;
@nav-color: #fff;


#Top {
    bottom: 2rem;
}
/* wrapper */

.c2c-wrapper {
    position: relative;
    margin: 0 auto;
    // margin-bottom: 1.8rem;
}

/* loading 滚动条 */
.sn-loading-type {
    margin-top: 12px;
    margin-bottom: 2.2rem;
}

.sn-loading-type-top {
    .alert-refresh {
        display: none;
        width: 100%;
        height: 1.6rem;
        position: absolute;
        z-index: 100;
        background: #FFF2B7;
        text-align: center;
        line-height: 1.6rem;
        margin-bottom: -12px;

        a {
            font-size: .56rem;
            color: #2e8ffd;

            span {
                color: #333;
            }
        }

    }
    .pull-loading {
        display: none;
        padding: 0.6rem 0;
        margin-top: 12px;
        margin-bottom: -12px;

        .sn-pull-loading {
            height: .48rem;
            line-height: .48rem;
        }
    }
}

/* 全国和分类索引 */

.fixed-nav {
    width: 100%;
    height: 2.4rem;
    position: relative;
}
.c2c-index {
    position: relative;
    width: 100%;
    height: 2.4rem;
    background: #fbfbfb;
    z-index: 90;

    &.sticky-fix {
        position: fixed;
        top: 0;
        -webkit-transition: top .5s ease;
        &.slideTop {
            top: 0rem;
        }
        &.slideBottom {
            top: -2.4rem;
        }
    }
    .city-bg {
        width: 100%;
        height: 6rem;
    }
    .search-grid {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 7.68rem;
        height: 1.04rem;
        margin-top: -.52rem;
        margin-left: -3.84rem;
        text-align: center;
        border: 1px solid #74C546;
        border-radius: 5px;
        font-size: 0;
        line-height: 1.04rem;
        box-sizing: content-box;
        overflow: hidden;
        background: #74C546;

    }
    .area-search,
    .category-search {
        font-size: .56rem;
        display: inline-block;
        width: 49%;
        height: 100%;
        color: #fff;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;

        span {
            width: 80%;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            display: inline-block;
            vertical-align: middle;
        }

        &:after {
            display: inline-block;
            width: 0;
            height: 0;
            margin-left: .2rem;
            content: '';
            border-top: .32rem solid #fff;
            border-right: .2rem solid transparent;
            border-left: .2rem solid transparent;
        }
    }
    .area-search {
        border-right: 1px solid #fff;
    }
}
/* 商品列表 */

.c2c-items-wrapper {
    position: relative;
    margin-top: -14px;
}

.c2c-item {
    background: #fff;
    margin-top: 14px;
}

.app-scroller {
    box-sizing: content-box;
    padding: .4rem 0;

    ul {
        padding: 0 .2rem;

        li {
            margin: 0 .2rem;
        }

        img {
            width: 5.6rem;
            height:5.6rem;
        }
    }
}

.item-middle {
    .app-scroller {
        img {
            width: 6rem;
            height: 6rem;
        }
    }
}

.item-info {
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    padding: .5rem;
    box-sizing: content-box;
}

.seller-info {
    height: 1.25rem;
    color: #666;

    .seller-fnl, .seller-ta {
        display: block;

    }

    .seller-fnl {
        float: left;
        color: #333;
    }
    .seller-ta {
        float: right;
        color: #333;
    }
    .seller-favicon {
        width: 1.25rem;
        height: 1.25rem;
        border-radius: 50%;
        overflow: hidden;
        float: left;

        img {
            width: 1.28rem;
            height: 1.28rem;
        }
    }
    .seller-nickname,
    .seller-area,
    .seller-time {
        float: right;
        line-height: 1.25rem;
        font-size: 0.56rem;
        margin-left: .32rem;

    }

    .seller-area {
        width: 3.4rem;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        text-align: right;
        margin-left: 0;
    }
    .seller-nickname {
        float: left;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        max-width: 2.5rem;
    }

    .seller-level {
        float: left;
        width: .64rem;
        height: .64rem;
        background: #000;
        margin: .3rem;
        i {
            display: inline-block;
            width: .64rem;
            height: .64rem;
            background-image: url(images/icon-level-00.png);
            background-size: .64rem;
        }

    }

    .seller-level-00 {
        i {
            background-image: url(images/icon-level-00.png);
        }
    }
    .seller-level-01 {
        i {
            background-image: url(images/icon-level-01.png);
        }
    }
    .seller-level-02 {
        i {
            background-image: url(images/icon-level-02.png);
        }
    }
    .seller-level-03 {
        i {
            background-image: url(images/icon-level-03.png);
        }
    }
}

.product-info {
    display: block;

    .product-title {
        font-size: .6rem;
        line-height: 1.2rem;
        height: 1.2rem;
        overflow: hidden;
        color: #333;
        word-break: keep-all;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .product-desc {
        font-size: .56rem;
        line-height: 0.8rem;
        max-height: 1.6rem;
        overflow: hidden;
        color: #666;

        word-wrap: break-word;
        word-break: normal;
    }

    .product-price {
        line-height: .56rem;
          margin-top: 0.4rem;
    }
    .sale-price {
        font-size: .72rem;
        color: #ff3000;

        .sml-pri {
            font-size: .56rem;
        }
    }
    .original-price {
        font-size: .56rem;
        color: #999;
        text-decoration: line-through;
        margin-left: .16rem;
    }
}

.item-action {
    width: 100%;
    height: 1.6rem;
    line-height: 1.6rem;
    margin-left: -1px;
    text-align: center;
    border-bottom: 1px solid #eee;

    a {
        float: left;
        display: block;
        width: 33.33%;
        height: 100%;
        border-left: 1px solid #eee;
    }
    i,
    em {
        vertical-align: middle;
    }
    i {
        display: inline-block;
        width: .64rem;
        height: .64rem;
        background-size: cover;
    }
    em {
        margin-left: .2rem;
        color: #929292;
        font-size: .56rem;
    }
    .action-like {
        i {
        background-image: url(data:image/png;base64,);
        }
        &.active i {
            background-image: url(data:image/png;base64,);
        }
    }
    .action-chat i {
        background-image: url(data:image/png;base64,);
    }
    .action-share i {
        background-image: url(data:image/png;base64,);
    }
}

.pic-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;

    .index-slide-box {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;

        img {
            position: relative;
            width: 15rem;
            height: 10rem;
            transform: translate(-50%,0);
            left: 50%;
        }

        .slide_ul {
            width: 100%;
            height: 100%;
            display: -webkit-box;
            -webkit-box-orient: horizontal;
            margin: 0;
            overflow: hidden;
        }

        li {
            position: relative;
            float: left;
            overflow: hidden
        }
    }

    .index-slide-trigger {
        position: absolute;
        right: .1rem;
        top: .7rem;
        text-align: right;
        font-size: 0
    }

    .index-slide-trigger li {
        display: inline-block;
        margin-right: .2rem;
        width: .5rem;
        height: .2rem;
        border-radius: 2px;
        background-color: rgba(255,255,255,.7)
    }

    .index-slide-trigger li.cur {
        background-color: #fff
    }

    .seller-info {
        width: 100%;
        padding: 0 .5rem;
        position: absolute;
        bottom: 0;
        left: 0;
        background: #959595;
        background: rgba(0,0,0,.4);
        color: #fff;

        .seller-fnl {
            color: #fff;
        }
        .seller-ta {
            color: #fff;
        }

        .seller-favicon {
            width: 1.8rem;
            height: 1.8rem;
            border: 2px solid #fff;
            margin-top: -.7rem;

            img {
                width: 1.68rem;
                height: 1.68rem;
            }
        }
        .seller-nickname {
                        color: #fff;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            max-width: 2.5rem;
                    }
        .seller-level {
            margin: .25rem;
            border: 1px solid #fff;
            box-sizing: content-box;
        }
    }
}


.index-page .copyright {
    padding-bottom: 1.5rem
}


/* 地址选择 */
.c2c-address, .c2c-category {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 102%;
    background: #FFF;
    z-index: 100;
    -webkit-transform: translate3d(100%,0,0);
    -webkit-transition: all .5s ease-out;

    &.page-slideLeft {
        -webkit-transform: translate3d(0,0,0);
    }

    .overtouch {
        overflow-x: hidden;
        height: 568px;
    }
}

.of-y {
    overflow-y: hidden;
}

/* category */
.c2c-category {
    .list-items {
        width: 5rem;
        background: #fff;
        .cur {
            color: #74C546;
            background: #eaeaea;

            &:before {
                display: none;
            }
        }
    }

    .list-details {
        padding: 0;
        background: #eaeaea;
        .list-label {
            dl {
                margin-top: 0;
            }
            li {
                float: none;
                position: relative;
                margin: 0;
                width: 100%;
                height: 1.8rem;
                line-height: 1.8rem;
                border-bottom: 1px solid #DCDCDC;
                text-indent: 2rem;
                background: #eaeaea;

                span {
                    font-size: .6rem;
                    padding: 0;
                    height:1.8rem;
                    line-height: 1.8rem;
                }

                &.cur {
                    span {
                        color: #74C546;
                    }
                }

                &.cur:after {
                    position: absolute;
                    right: 0;
                    top:0;
                    display: block;
                    content: "";
                    width: .64rem;
                    height: .44rem;
                    margin: .58rem;
                    background-image: url(images/icon-list-cur.png);
                    background-size: .64rem .44rem;
                }
            }
        }

    }
}

/* 遮罩层 */

/**点击图片放大**/
.maskSwipe{
    position: absolute;
    top: 1.6rem;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 8;
}
.swiper-container{
    overflow: hidden;
    position: relative;width: 428px;
    height: 667px;
}
.maskSwipe .swiper-container .swiper-slide{
    position: absolute;
    overflow: hidden;
    display:table;
    vertical-align:middle;
    text-align:center;
}
.maskSwipe .swiper-slide span{
    display:table-cell;
    vertical-align:middle;
}
.maskSwipe .swiper-container .swiper-cancel{
    position: absolute;
    bottom:10px;
    left: 5%;
    width: 90%;
    height: 1.5rem;
    line-height: 1.5rem;
    border-radius: 5px;
    font-size: .6rem;
    text-align: center;
    background:#CCC;
    color: #333;
    z-index: 10;
}
.maskSwipe .swiper-container .swiper-cancel:active{
    background: #DDD;
}
.maskSwipe .swiper-container .swiper-pagination{
    bottom:2.5rem;
}
.maskSwipe .swiper-pagination .swiper-pagination-bullet{
    background:#FFF;
    opacity:1;
}
.maskSwipe .swiper-pagination .swiper-pagination-bullet-active{
    background:#74C546;
}
